.copy-button {
  background-color: colorPalette('gray', 300);
  padding: $space * 1.5;
  border-radius: $border-radius;
  display: flex;
  gap: $space * 3;
  transition: background-color 0.25s ease-in-out;
  cursor: pointer;

  @include breakpoint-up(lg) {
    gap: $space * 8.625;
  }

  &:hover {
    background-color: colorPalette('gray', 350);
    outline: 2px dashed colorPalette('gray', 500);
    outline-offset: -3px;

    &:active {
      background-color: colorPalette('gray', 400);
    }

    .copy-button__icon {
      animation: 1s ease-in-out infinite up-and-down;
    }
  }

  &__text {
    font-size: fontSize(normal);
    line-height: 16.71px;
  }
}
