.header__settings, .request-card--blank {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@keyframes up-and-down {
  0% {
    transform: translateY(1px);
  }
  50% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(1px);
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}

*::before,
*,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #fafafa;
  color: #222222;
  font-size: 16px;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  cursor: pointer;
  text-decoration: #069e9e;
}

button {
  all: unset;
}

.button, .button--borderless, .button--success, .button--secondary, .button--primary {
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.565rem;
  text-align: center;
  color: #fff;
  padding: 8px 24px;
  border-radius: 4px;
  transition: all 0.1s ease-in-out;
  cursor: pointer;
}
@media (min-width: 992px) {
  .button, .button--borderless, .button--success, .button--secondary, .button--primary {
    font-size: 16px;
  }
}

.button--primary {
  background-color: #0069ff;
}
.button--primary:hover {
  background-color: #4490fc;
}
.button--primary:focus {
  background-color: #0459d2;
}

.button--secondary {
  background-color: #fff;
  color: #222222;
  border: 1px solid #b8b8b8;
}
.button--secondary:hover {
  background-color: #fafafa;
}
.button--secondary:focus {
  background-color: #eeeeee;
}

.button--success {
  background-color: #01af1b;
}
.button--success:hover {
  background-color: #00cc1e;
}
.button--success:focus {
  background-color: #009516;
}

.button--borderless {
  font-weight: 500;
  color: #666666;
  padding: 0;
}
.button--borderless:hover {
  color: #b8b8b8;
}

.request-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
  padding-left: 32px;
  padding-right: 32px;
  margin-bottom: 16px;
  color: #222222;
}
.request-card__body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px 0;
}
@media (min-width: 768px) {
  .request-card__body {
    flex-direction: row;
  }
}
.request-card__body .request__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .request-card__body .request__info {
    margin-bottom: 0;
  }
}
.request-card__body .request__info-date, .request-card__body .request__info-date--danger {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 4px;
}
.request-card__body .request__info-date--danger {
  color: #eb1717;
}
.request-card__body .request__info-subject {
  font-size: 16px;
  font-weight: 400;
}
.request-card__body .requester__info-name {
  font-size: 14px;
  font-weight: 600;
  color: #666666;
}
.request-card__body .requester__info-mail {
  font-size: 14px;
  font-weight: 400;
}
.request-card__details {
  padding: 24px 0;
  border-top: 1px solid #eeeeee;
}
.request-card__details .details__question {
  margin-bottom: 24px;
}
.request-card__details .details-title {
  display: block;
  font-size: 12px;
  color: #666666;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.request-card__details .details-info {
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
.request-card__footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #eeeeee;
  padding: 16px 0;
}
.request-card__footer-details {
  font-size: 16px;
  color: #666666;
  text-decoration: underline;
  cursor: pointer;
}
.request-card__footer-details:hover {
  color: #b8b8b8;
}
.request-card__footer-actions .reject {
  margin-right: 24px;
}
@media (min-width: 768px) {
  .request-card__footer-actions .reject {
    margin-right: 40px;
  }
}

.request-card--blank {
  color: #666666;
  padding: 32px 0;
  border: 2px dashed #b8b8b8;
  margin-bottom: 16px;
  border-radius: 8px;
}

.request-card--disabled {
  opacity: 0.5;
  pointer-events: none;
}

.copy-button {
  background-color: #eeeeee;
  padding: 12px;
  border-radius: 4px;
  display: flex;
  gap: 24px;
  transition: background-color 0.25s ease-in-out;
  cursor: pointer;
}
@media (min-width: 992px) {
  .copy-button {
    gap: 69px;
  }
}
.copy-button:hover {
  background-color: #e2e1e1;
  outline: 2px dashed #666666;
  outline-offset: -3px;
}
.copy-button:hover:active {
  background-color: #b8b8b8;
}
.copy-button:hover .copy-button__icon {
  animation: 1s ease-in-out infinite up-and-down;
}
.copy-button__text {
  font-size: 14px;
  line-height: 16.71px;
}

.header {
  padding: 12px 32px 12px 32px;
  border-bottom: 1px solid #efefef;
}
.header .wrapper {
  max-width: 1440px;
  margin-inline: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  flex-shrink: 0;
}
.header__share {
  display: none;
}
@media (min-width: 768px) {
  .header__share {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;
    margin-left: auto;
    margin-right: 16px;
  }
}
@media (min-width: 992px) {
  .header__share {
    margin-right: auto;
    margin-left: 80px;
  }
}
.header__share-text {
  display: none;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 992px) {
  .header__share-text {
    display: block;
  }
}
.header__add-button {
  margin-right: 0;
  display: none;
}
@media (min-width: 768px) {
  .header__add-button {
    display: block;
    margin-right: 48px;
  }
}
.header__settings .settings__profile {
  margin-right: 12px;
}
.header__settings .setting__icon {
  transition: transform 0.5s ease-in-out;
  cursor: pointer;
}
.header__settings .setting__icon:hover {
  transform: rotate(-90deg);
}

.scheduled-events__upcoming .display, .scheduled-events__pending .display {
  font-size: 32px;
  line-height: 38.19px;
}

.scheduled-events {
  padding: 56px 0;
}
@media (min-width: 768px) {
  .scheduled-events {
    padding: 56px 0 56px 96px;
  }
}
.scheduled-events .wrapper {
  display: grid;
  gap: 24px;
}
@media (min-width: 768px) {
  .scheduled-events .wrapper {
    grid-template-columns: 4fr 2fr;
    gap: 61px;
  }
}
.scheduled-events .label {
  font-size: 12px;
  color: #0069ff;
  text-transform: uppercase;
  line-height: 14.32px;
  margin-bottom: 8px;
}
.scheduled-events__pending .display {
  color: #222222;
  margin-bottom: 16px;
}
.scheduled-events__pending .pending__explanation {
  font-size: 16px;
  color: #666666;
  line-height: 26px;
}
.scheduled-events__upcoming .display {
  color: #b8b8b8;
}

main {
  max-width: 936px;
  margin-inline: auto;
}
main section {
  margin-bottom: 3rem;
}
main h1 {
  margin-bottom: 3rem;
}
main h2 {
  margin-bottom: 2rem;
}
main h3 {
  margin-top: 4rem;
  margin-bottom: 1rem;
}

section {
  width: 90%;
  max-width: 936px;
  margin-inline: auto;
}

.link-to-components {
  position: fixed;
  display: inline-block;
  bottom: 10px;
  right: 10px;
  animation: up-and-down 1s ease-in-out infinite;
  z-index: 3;
}