.button {
  display: inline-block;
  font-size: fontSize(normal);
  font-weight: 600;
  line-height: 1.565rem;
  text-align: center;
  color: #fff;
  padding: $space $space * 3;
  border-radius: $border-radius;
  transition: all 0.1s ease-in-out;
  cursor: pointer;

  @include breakpoint-up(lg) {
    font-size: fontSize(medium);
  }
}

.button--primary {
  @extend .button;
  background-color: color(primary);

  &:hover {
    background-color: color(primary-lighten);
  }

  &:focus {
    background-color: color(primary-darken);
  }
}

.button--secondary {
  @extend .button;
  background-color: colorPalette('gray', 100);
  color: colorPalette('gray', 600);
  border: 1px solid colorPalette('gray', 400);

  &:hover {
    background-color: colorPalette('gray', 200);
  }

  &:focus {
    background-color: colorPalette('gray', 300);
  }
}

.button--success {
  @extend .button;
  background-color: color(success);

  &:hover {
    background-color: color(success-lighten);
  }

  &:focus {
    background-color: color(success-darken);
  }
}

.button--borderless {
  @extend .button;
  font-weight: 500;
  color: colorPalette('gray', 500);
  padding: 0;

  &:hover {
    color: colorPalette('gray', 400);
  }
}
