@use 'sass:math';

.request-card {
  background-color: #fff;
  border-radius: $border-radius * 2;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
  padding-left: $space * 4;
  padding-right: $space * 4;
  margin-bottom: $space * 2;
  color: colorPalette('gray', 600);

  &__body {
    @include flexbox(column, space-between, flex-start);
    padding: $space * 3 0;

    @include breakpoint-up(md) {
      flex-direction: row;
    }

    .request__info {
      @include flexbox(column, center);
      gap: math.div($space, 2);
      margin-bottom: $space * 2;

      @include breakpoint-up(md) {
        margin-bottom: 0;
      }

      &-date {
        font-size: fontSize(normal);
        font-weight: 600;
        margin-bottom: math.div($space, 2);
      }

      &-date--danger {
        @extend .request__info-date;
        color: color(danger);
      }

      &-subject {
        font-size: fontSize(medium);
        font-weight: 400;
      }
    }

    .requester__info {
      &-name {
        font-size: fontSize(normal);
        font-weight: 600;
        color: colorPalette('gray', 500);
      }

      &-mail {
        font-size: fontSize(normal);
        font-weight: 400;
      }
    }
  }

  &__details {
    padding: $space * 3 0;
    border-top: 1px solid colorPalette('gray', 300);

    .details__question {
      margin-bottom: $space * 3;
    }

    .details-title {
      display: block;
      font-size: fontSize(small);
      color: colorPalette('gray', 500);
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: $space;
    }

    .details-info {
      display: block;
      font-size: fontSize(medium);
      font-weight: 400;
      line-height: $space * 3;
    }
  }

  &__footer {
    @include flexbox(row, space-between, center);
    border-top: 1px solid colorPalette('gray', 300);
    padding: $space * 2 0;

    &-details {
      font-size: fontSize(medium);
      color: colorPalette('gray', 500);
      text-decoration: underline;
      cursor: pointer;

      &:hover {
        color: colorPalette('gray', 400);
      }
    }

    &-actions {
      .reject {
        margin-right: $space * 3;

        @include breakpoint-up(md) {
          margin-right: $space * 5;
        }
      }
    }
  }
}

// Blank Card Component
.request-card--blank {
  @extend %flexboxRowCenter;
  color: colorPalette('gray', 500);
  padding: $space * 4 0;
  border: 2px dashed colorPalette('gray', 400);
  margin-bottom: $space * 2;
  border-radius: $border-radius * 2;
}

// Disabled Card Component
.request-card--disabled {
  opacity: 0.5;
  pointer-events: none;
}
