.header {
  padding: $space * 1.5 $space * 4 $space * 1.5 $space * 4;
  border-bottom: 1px solid #efefef;

  .wrapper {
    max-width: map-get($grid-breakpoints, xxl);
    margin-inline: auto;
    @include flexbox(row, space-between, center);
  }

  &__logo {
    flex-shrink: 0;
  }

  &__share {
    display: none;

    @include breakpoint-up(md) {
      @include flexbox(row, null, center);
      gap: $space * 2;
      margin-left: auto;
      margin-right: $space * 2;
    }

    @include breakpoint-up(lg) {
      margin-right: auto;
      margin-left: $space * 10;
    }

    &-text {
      display: none;
      font-size: fontSize(normal);
      line-height: 18px;

      @include breakpoint-up(lg) {
        display: block;
      }
    }
  }

  &__add-button {
    margin-right: 0;
    display: none;

    @include breakpoint-up(md) {
      display: block;
      margin-right: $space * 6;
    }
  }

  &__settings {
    @extend %flexboxRowCenter;

    .settings__profile {
      margin-right: $space * 1.5;
    }

    .setting__icon {
      transition: transform 0.5s ease-in-out;
      cursor: pointer;
      &:hover {
        transform: rotate(-90deg);
      }
    }
  }
}
